.dropShadow {
  box-shadow: 8px 11px 5px -4px rgba(0, 0, 0, 0.41);
  -webkit-box-shadow: 8px 11px 5px -4px rgba(0, 0, 0, 0.41);
  -moz-box-shadow: 8px 11px 5px -4px rgba(0, 0, 0, 0.41);
}
h1,
h2,
h3,
h4 {
  font-family: "Arvo", serif;
  color: #afafaf;
}

h5 {
  background-color: #585858;
  opacity: 0.7;
  padding: 2%;
  border-radius: 7px;
  text-align: center;
  width: 50%;
  margin: auto;
}

