  margin-bottom: 60px;
}

/* .testimonialHeader {
  top: -700px;
} */

.parallax-container {
  height: 550px;
}

.parallax-content {
  z-index: 10;
  position: absolute;
}
.parallax-content-box {
  width: 750px;
  margin: 10% 30% 10% 30%;
  background-color: #585858;
  border-radius: 7px;
  opacity: 0.7;
}
.testimonialText {
  color: white;
  padding: 5%;
  font-family: "Arvo", serif;
  width: 100%;
}

.testimonialText span {
  font-style: italic;
  font-size: 0.8em;
}
@media only screen and (max-width: 1000px) {
  .parallax-content-box {
    width: 600px;
    margin: 10% 30% 10% 30%;
  }
}

